.hero {
  padding: 4.6rem 18.6rem 12.5rem 7.25rem;
  width: 67.917vw;
  max-width: 978px;
  height: 737px;
  border-radius: 0px 0px 35px 35px;
  background: linear-gradient(315deg, #d6e6fe 0%, rgba(214, 252, 254, 0) 100%);
  overflow: hidden;
  justify-self: flex-start;
  margin-left: -300px;
  position: relative;
  overflow: visible;
}

/* 4.688rem 18.625rem 12.5rem 13.5rem; */
.logo {
  /* width: 4.444vw; */
  max-width: 64px;
  margin-bottom: 7.9rem;
}

.title {
  margin-bottom: 2.188rem;
  color: #253347;
  font-size: 4rem;
  font-style: normal;
  font-weight: 600;
  line-height: 4.375rem;
  letter-spacing: -3.2px;
  /* white-space: nowrap; */
}

.text {
  max-width: 465px;
  min-width: 190px;
  color: #5e6e85;
  font-size: 1rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5rem;
}

.heroImperialMargin {
}

@media screen and (max-width: 1440px) {
  .title {
    font-size: 4.444vw;
    line-height: 4.889vw;
  }
}

@media screen and (max-width: 820px) {
  .logo {
    margin-bottom: 2.5rem;
    width: 5vw;
    min-width: 40px;
  }

  .hero {
    margin-left: 0px;
    position: relative;
    padding: 2rem 2.7rem 20.3rem 2rem;
    align-items: center;
    display: flex;
    flex-direction: column;
    max-width: 100%;
    width: 100%;
    height: 640px;
  }

  .title {
    text-align: center;
    font-size: 64px;
    line-height: 110%;
  }

  .text {
    font-size: 1rem;
    max-width: 686px;
    text-align: center;
  }
}

@media screen and (max-width: 560px) {
  .title {
    font-size: 2.5rem;
  }
}

@media screen and (max-width: 375px) {
  .hero {
    /* margin-bottom: 450px;  */
  }
}
