.app {
  /* margin: 0px 24px; */
}

.hero {
  display: flex;
  justify-content: center;
  margin-bottom: 65px;
  position: relative;
  /* flex-shrink: 0; */
  /* margin-left: -400px; */
  /* max-height: 737px; */
}

.resultDesctiptionHolder {
  margin-bottom: 96px;
  display: flex;
  justify-content: center;
}

.adviceCardsHolder {
  margin-bottom: 120px;
  display: flex;
  justify-content: center;
}

.limitationsHolder {
  margin-bottom: 120px;
  display: flex;
  justify-content: center;
}

.limitations {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
}

.limitationsTextContent {
  max-width: 564px;
}

.limitationsTitle {
  margin-bottom: 35px;
  color: #253347;
  font-size: 48px;
  font-style: normal;
  font-weight: 600;
  line-height: 52.8px;
  letter-spacing: -2.4px;
}

.limitationsText {
  color: #5e6e85;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.rowOne {
  margin-bottom: 32px;
  display: flex;
  align-items: flex-start;
  gap: 132px;
}
.rowTwo {
  margin-bottom: 24px;
  display: flex;
  gap: 32px;
  /* justify-content: flex-end; */
  margin-left: 400px;
}

.rowThree {
  display: flex;
  gap: 32px;
  justify-content: center;
}

.decorLineLeft {
  position: relative;
  right: 0px;
}

@media screen and (max-width: 1260px) {
  .hero {
    flex-direction: column;
    align-items: center;
  }

  .limitationsHolder {
    margin: 0px 39px;
    margin-bottom: 95px;
  }
}

@media screen and (max-width: 820px) {
  .hero {
    margin-bottom: 277px;
  }

  .limitationsCards {
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: repeat(2, 1fr);
    row-gap: 15px;
    column-gap: 24px;
    margin-bottom: 24px;
  }

  .limitationsTextContent {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 3.5rem;
  }

  .limitationsTitle {
    font-size: 2rem;
    margin-bottom: 2rem;
  }

  .limitationsText {
    text-align: center;
  }

  .fifthLimitationCardHolder {
    display: flex;
    justify-content: center;
  }

  #hey {
    background-color: aqua;
  }
}

@media screen and (max-width: 560px) {
  .limitationsHolder {
    margin: 0px 19px;
    margin-bottom: 95px;
  }

  .limitations {
    display: flex;
    flex-direction: column;
  }

  .limitationsCards {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .hero {
    margin-bottom: 450px;
  }
}

