.adviceCards {
  display: flex;
  gap: 32px;
  border-radius: 35px;
  padding: 72px 117px 96px 116px;
  /* background: linear-gradient(315deg, #D6E6FE 0%, rgba(214, 252, 254, 0.00) 10%); */
  background: linear-gradient(315deg, #d6e6fe 0%, rgba(214, 252, 254, 0) 50%);
}

.adviceCard {
  max-width: 365px;
}

.adviceCardImg {
  margin-bottom: 45px;
}

.title {
  margin-bottom: 24px;
  color: #253347;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: -1.2px;
}

.text {
  color: #5e6e85;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

@media screen and (max-width: 820px) {
  .adviceCards {
    flex-direction: column;
    align-items: flex-start;
    padding: 60px 43px 60px 39px;
    width: 100%;
    border-radius: 0%;
  }

  .adviceCard {
    max-width: 100%;
    display: flex;
    gap: 40px;
  }

  .adviceCardImg {
    margin-bottom: 0px;
  }
}

@media screen and (max-width: 390px) {
  .adviceCards {
    background: #fff;
    padding: 0px 19px;
    gap: 40px;
  }

  .adviceCard {
    flex-direction: column;
  }

  .adviceCardImg {
    max-width: 64px;
  }
}
